.themoin-agency {
  position: absolute;
  width: 100%;
  min-width: 900px;
  min-height: calc(100vh - 75px);
  background-color: $pale;

  display: flex;
  flex-direction: column;
  align-items: center;
  color: $dark-blue;

  h1, h2, h3, p {
    margin: 0px;
    padding: 0px;
  }

  h1 {
    font-size: 36px;
    font-weight: 100;
    line-height: 1.33;
    letter-spacing: -1.5px;
  }

  .header {
    position: fixed;
    z-index: 1030;
    top: 0;
    left: 0;
    width: 100%;
    height: 75px;
    display: flex;
    flex-direction: row;
    align-items: center;
    border: solid 1px $light-grey;
    border-width: 0 0 1px 0;
    background-color: $white;
    letter-spacing: -0.5px;

    .logo:hover { opacity: 1; }
    .logo img {
      height: 22px;
      margin-left: 28px;
      margin-right: 15px;
      transform: translateY(-2px);
    }

    .title {
      font-size: 16px;
    }

    .sub-title {
      margin-right: 28px;
      font-weight: bold;
    }

    .total-amount {
      margin-right: 36px;
      color: #6e799a;
      .money { color: $dark-blue; }
    }

    a {
      margin-right: 30px;
    }

    .spacer { flex-grow: 1 }
  }

  .label-duration {
    margin-top: 39px;
    margin-bottom: 10px;
  }

  .date-search {
    margin-top: 23px;
    display: flex;
    flex-direction: row;
    justify-content: center;

    > select, a {
      margin: 0 3px;
    }
  }

  select {
    width: 100px;
    height: 40px;
    border: solid 1px $line-grey;
    border-radius: 0;
    appearance: none;
    padding-left: 13px;
    padding-right: 10px;
    background-color: $white;

    background-image: url('/img/ic-dropdown.svg');
    background-repeat: no-repeat;
    background-origin: content-box;
    background-position: center right;
  }

  .button {
    width: 68px;
    height: 40px;
    background-color: $purple;
    border: solid 1px $purple;
    color: $white;

    display: flex;
    align-items: center;
    justify-content: center;
  }

  .button:active {
    opacity: 0.8;
  }

  .button.complete {
    color: $grey;
    background-color: $pale;
    border: solid 1px $line-grey;
  }

  .button.complete:hover {
    opacity: 1.0;
    cursor: not-allowed;
  }

  .list {
    margin-top: 36px;
    width: 100%;
    min-height: 800px;
    background-color: $white;
    border: solid 1px $light-grey;
    border-width: 0 0 1px 0;
  }

  table {
    width: 100%;
    font-size: 14px;
  	letter-spacing: -0.5px;

    th {
      font-weight: normal;
    }

    tr {
      height: 50px;
      border-style: solid;
      border-width: 0 0 1px 0;
      border-color: $light-grey;
    }

    tr:nth-child(even) {
      background-color: $pale;
    }

    thead > tr {
      border-width: 1px 0 1px 0;
    }

    tbody > tr:nth-child(15) {
      border-width: 0px;
    }

    td:first-child, th:first-child {
      padding-left: 28px;
    }

    td:last-child {
      width: 89px;
    }

    td.pending {
      color: $grey;
    }
  }

  .footer {
    margin-top: 27px;
    height: 40px;
    margin-bottom: 51px;

    display: flex;
    flex-direction: row;
    width: 100%;

    .spacer {
      flex-grow: 1;
      flex-basis: 357px;
    }

    .spacer:first-child {
      flex-shrink: 1000;
    }

    .pagination {
      margin: 0 10px;
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: center;

      > a {
        width: 24px;
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 0 8px;
        color: $grey;
      }

      a.current { color: $dark-blue; }
      a:active { opacity: 0.8; }

      .first, .prev, .next, .last {
        background-repeat: no-repeat;
        background-position: center center;
      }

      .first {
        background-image: url('/img/btn-recent-page.svg');
        margin-right: 2px;
      }
      .prev { background-image: url('/img/btn-previous-page.svg'); }
      .next { background-image: url('/img/btn-next-page.svg'); }
      .last {
        background-image: url('/img/btn-first-page.svg');
        margin-left: 2px;
      }

    }

    form {
      display: flex;
      flex-direction: row;
      justify-content: flex-end;

      input {
        border: solid 1px $grey;
      }

      input[type='text'] {
        width: 260px;
        margin-right: 8px;
        padding-left: 13px;

        &::placeholder {
          font-size: 14px;
        }
      }

      input[type='submit'].button {
        background-color: $white;
        color: $deep-grey;
        margin-right: 21px;
      }
    }
  }
}


