.themoin-info {
  @extend .themoin-landing;

  .info-intro {
    text-align: center;

    h1 {
      margin-top: 78px;
      margin-bottom: 34px;
      @media (max-width: $max-width) {
        margin-top: 28px;
        margin-bottom: 27px;
      }
    }

    p {
      margin-left: 16px;
      margin-right: 16px;

      @media (max-width: $max-width) {
        margin-bottom: 32px;
      }
    }

    img {
      margin-top: 40px;
      margin-bottom: 69px;
      @media (max-width: $max-width) {
        display: none;
      }
    }
  }

  .info-howto {
    text-align: center;
    margin-bottom: 100px;

    > div {
      display: flex;
      flex-direction: column;
      align-items: center;
    }

    h1 {
      margin-top: 52px;
      margin-bottom: 21px;
    }

    .images {
      display: flex;
      flex-direction: row;
      justify-content: center;
      flex-wrap: wrap;
      max-width: 900px;

      > div {
        width: 242px;
        height: 290px;
        margin: 20px;
        background-size: 800px auto;
        background-image: url(https://img.themoin.com/public/img/howtouse-moin@3x.png);
        background-repeat: no-repeat;
      }

      .step2 { background-position: -277px 0; }
      .step3 { background-position: -555px 0; }
      .step4 { background-position: 0 -363px; }
      .step5 { background-position: -277px -363px; }
      .step6 { background-position: -555px -363px; }
    }
  }
}


