.themoin-remit-form-recipients-add {
  @extend .themoin-recipients-update;

  padding: 0 10px;

  .form-contents {
    display: flex;
    width: 100%;
    flex-direction: column;

    > div {
      width: 100%;
    }
  }

  .form-row {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    &:last-child {
      margin-bottom: 30px;
    }

    > div {
      margin-top: 30px;
      padding-left: 10px;
      padding-right: 10px;
      flex-grow: 1;

      @media (max-width: $max-width) {
        margin-top: 20px;
      }
    }

    &.col-2 > div{
      width: 40%;
      min-width: 135px;
      flex: 1 1 135px;
    }
  }
}


