/* Text styles */

.moin-header {
  font-size: 36px;
  font-style: normal;
  font-stretch: normal;
  font-weight: 300;
  line-height: 1.17;
  letter-spacing: -3.4px;
  color: $dark-blue;
}

h1, .moin-title {
  font-size: 26px;
  line-height: 1.0;
  letter-spacing: -1px;
  color: $dark-blue;
  @media (max-width: $max-width) {
    font-size: 20px;
    line-height: 1.0;
    letter-spacing: -0.5px;
  }
}

h2, .moin-subtitle {
  font-size: 18px;
  line-height: 1.0;
  letter-spacing: -1px;
  color: $dark-blue;
  @media (max-width: $max-width) {
    font-size: 16px;
    line-height: 1.0;
    letter-spacing: -0.5px;
  }
}

h3, .hd-subtitle {
  font-size: 18px;
  line-height: 1.0;
  letter-spacing: -0.5px;
  color: $dark-blue;
  @media (max-width: $max-width) {
    font-size: 16px;
    line-height: 1.0;
    letter-spacing: -0.5px;
  }
}

p, .moin-body {
  font-size: 14px;
  line-height: 1.71;
  letter-spacing: -0.5px;
  color: $deep-grey;
  @media (max-width: $max-width) {
    font-size: 13px;
    line-height: 1.69;
    letter-spacing: -0.5px;
  }
}

.caption {
  font-size: 14px;
  line-height: 1.0;
  letter-spacing: -0.5px;
  color: $deep-grey;

  @media (max-width: $max-width) {
    font-size: 13px;
    line-height: 1.0;
    letter-spacing: -0.5px;
    color: $deep-grey;
  }
}

a {
  color: $deep-grey;
}

a:hover, button:hover {
  cursor: Pointer;
  opacity: 0.5;
}

a:focus {
  text-decoration: none;
}

.mobile h1, .mobile-header {
  font-size: 24px;
  line-height: 1.08;
  letter-spacing: -2px;
  color: $dark-blue;
}

.mobile-title {
  font-size: 20px;
  line-height: 1.0;
  letter-spacing: -0.5px;
  color: $dark-blue;
}

.hd-input-field {
  font-size: 18px;
  line-height: 1.0;
  letter-spacing: -0.5px;
  color: $grey;
}

.mobile-subtitle {
  font-size: 16px;
  line-height: 1.0;
  letter-spacing: -0.5px;
  color: $dark-blue;
}

.hd-button {
  font-size: 14px;
  line-height: 1.0;
  letter-spacing: -0.5px;
  color: white;
}

.h-d-text-button {
  font-size: 14px;
  line-height: 1.0;
  letter-spacing: -0.5px;
  color: $deep-grey;
}

.mobile-body {
  font-size: 13px;
  line-height: 1.69;
  letter-spacing: -0.3px;
  color: $deep-grey;
}

.mobile-text-button {
  font-size: 13px;
  line-height: 1.0;
  letter-spacing: -0.5px;
  color: $deep-grey;
}


