.themoin-currency-component {
  @extend .themoin-container;
  height: 190px;
  margin-top: 35px;
  margin-bottom: 25px;
  @media (max-width: $max-width) {
    margin-top: 30px;
    margin-bottom: 40px;
  }

  > div {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
  }

  .legend {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    font-size: 12px;
    @media (max-width: $max-width) {
      top: 200px;
      display: flex;
      flex-direction: row;
      font-size: 10px;
    }

    > div {
      width: 94px;
      @media (max-width: $max-width) {
        width: 80px;
      }
      padding-left: 7px;
      height: 15px;
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;

      &:first-child {
        margin-bottom: 10px;
        @media (max-width: $max-width) {
          margin-bottom: 0;
          margin-right: 5px;
        }
      }
    }

    > div > div {
      width: 26px;
      height: 2px;
    }

    .bank-avg {
      color: $grey;

      > div {
        background-color: $line-grey;
      }
    }

    .moin-avg {
      color: $deep-grey;

      > div {
        background-color: $grey;
      }
    }
  }

  .content {
    width: 280px;
    letter-spacing: -0.5px;
    text-align: center;
    z-index: 2;

    .title {
      font-size: 14px;
      @media (max-width: $max-width) {
        font-size: 13px;
      }
      color: $deep-grey;
    }

    .currency {
      margin-top: 5px;
      font-size: 18px;
      @media (max-width: $max-width) {
        font-size: 16px;
      }
      color: $dark-blue;
    }
  }

  .graph-dots, .graph-lines {
    height: 190px;
    position: absolute;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }

  .graph-dots { width: 100%; }
  .graph-lines {
    width: calc(100% - 22px);
    left: 10px;
  }

  .dot-box {
    width: 22px;
    height: 140px;
    position: relative;

    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;

    font-size: 12px;
    letter-spacing: -0.4px;
    color: $grey;

    .dot {
      width: 6px;
      height: 6px;
      border-radius: 50%;
    }

    .date {
      margin-top: 15px;
      height: 15px;
    }

    .currency-tooltip {
      display: none;
      width: 80px;
      height: 100px;
      padding-top: 12px;
      position: absolute;
      top: 10px;
      left: -30px;
      background-color: $dark-blue;
      opacity: 0.9;
      border-radius: 4px;

      flex-direction: column;
      align-items: center;

      font-size: 14px;
      color: white;
      line-height: 1;

      .bank {
        opacity: 0.5;
        margin-bottom: 15px;
      }
    }

    &:hover .currency-tooltip{
      display: flex;
      z-index: 3;
    }
  }

  .line-box {
    flex-grow: 1;
    height: 140px;
    margin-bottom: 30px;
    overflow: hidden;

    display: flex;
    flex-direction: column;
    justify-content: flex-end;

    .line {
      width: 170px;
      height: 1px;
      transform-origin: left center;
    }
  }

  .color-moin {
    background-color: $grey;
  }
  .color-bank {
    background-color: $line-grey;
  }
}


