@viewport {
  min-width: 320px;
}

html {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

body {
  min-width: 300px;
  padding-top: 76px;
  @media (max-width: $max-width) {
    padding-top: 53px;
  }
}

* {
  box-sizing: border-box;
}

ul {
  padding: 0;
}

li {
  list-style-type: none;
}

.moin-error {
  text-align: left;
  color: #EE1717;
  height: 0;
  transition: transform 0.3s, height 0.3s;
  transform-origin: top;
  transform: scale3d(1, 0, 1);
}

.moin-error.active {
  height: 20px;
  transform: scale3d(1, 1, 1);
}

.moin-input-group {
  margin-top: 15px;
  text-align: left;
}

.ui-helper-hidden-accessible {
  display: none;
}

.modal-btn {
  color: white;
}

/* chat plugin */
#moin-ch-plugin-root {
  #ch-plugin-button-position {
    right: 24px !important;
    bottom: 24px !important;
  }

  &.remit #ch-plugin-button-position {
    bottom: 83px !important;
  }

  &.hidden #ch-plugin-button-position {
    display: none;
  }
}

input[type='radio'] {
  display: none;
}

input[type='radio'] + label {
  font-size: 14px;
  line-height: 1.0;
  letter-spacing: -0.5px;
  color: $deep-grey;
  font-weight: normal;
  margin-right: 10px;
  cursor: pointer;
}

input[type='radio'] + label span {
  display: inline-block;
  width:24px;
  height:24px;
  margin:-2px 9px 0 0;
  vertical-align:middle;
  background: url('https://img.themoin.com/public/img/btn-radio.svg') top no-repeat;
}

input[type='radio']:checked + label span {
  background: url('https://img.themoin.com/public/img/btn-radio-s.svg') top no-repeat;
}

input[type='checkbox'] {
  margin-left: 0 !important; /* reset bootstrap!! */
  width: 18px;
  height: 18px;
  border: 1px solid $line-grey;
  -webkit-appearance: none;
  -moz-appearance: none;
}

input[type='checkbox']:checked {
  background-image: url('https://img.themoin.com/public/img/btn-checked.svg');
  background-repeat: no-repeat;
  background-position: -1px -1px;
}

input[type='checkbox'] + label {
  padding-left: 30px;
  transform: translateY(2px);
}



