.themoin-faq {
  @extend .themoin-container;

  p {
    margin: 0;
  }

  > div {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 80px;
  }

  .title {
    margin: 52px;
    @media (max-width: $max-width) {
      margin: 28px;
    }
  }

  ul.category {
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 80%;
    margin-bottom: 40px;
    @media (max-width: $max-width) {
      margin-bottom: 30px;
    }

    li {
      max-width: 150px;
      flex-grow: 1;
      flex-shrink: 1;
      text-align: center;
      font-size: 16px;
      > a { color: $deep-grey; }

      &.selected {
        > a { color: $dark-blue; }
      }
    }
  }

  .content {
    width: calc(100% - 28px);
    max-width: 780px;

    > div {
      width: 100%;
      border: solid 1px $light-grey;
      margin-bottom: 10px;
      cursor: pointer;
    }

    .question {
      display: flex;
      flex-direction: row;
      align-items: center;

      > p {
        flex-grow: 1;
        color: $dark-blue;
        margin: 18px 35px 18px 32px;
        @media (max-width: $max-width) {
          margin: 17px 9px 17px 19px;
        }
      }

      > img {
        width: 16px;
        height: 10px;
        margin-right: 18px;

        &.open {
          transform: rotateZ(180deg);
        }
      }
    }

    .answer {
      display: none;

      &.open {
        display: block;
      }

      > p {
        margin: 0 68px 18px 32px;
        @media (max-width: $max-width) {
          margin: 0px 18px 17px 19px;
        }
      }

      a {
        color: $dark-blue;
      }
    }

  }
}


