.moin-popup {
  > div {
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    z-index: 2000;
    background-color: rgba(0, 0, 0, 0.5);

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 0 20px;
  }

  .preview-popup {
    .popup-content {
      max-width: 720px;
    }
    /* preview-popup */
    .preview-table {
      font-size: 14px;
      font-weight: 400;
      letter-spacing: -1px;
      color: #a0a7b8;
    }

    .preview-table td {
      height:50px;
    }
  }

  .coupon-popup {
    .popup-content {
      height: 73px;
      max-width: 450px;
      width: 100%;
      padding: 0 25px;

      display: flex;
      flex-direction: row;
      align-items: center;
    }

    .coupon-input {
      width: calc(100% - 60px);
      height: 30px;
      font-size: 18px;
      line-height: 1;
      border: none;
      font-weight: 400;
      letter-spacing: -1px;
      color: #292767;
      vertical-align: middle;
    }

    .coupon-btn {
      font-size: 18px;
      font-weight: 400;
      line-height: 1;
      color: #292767;
      text-decoration: none;
    }
  }

  .popup-content {
    position: relative;
    height: auto;
    border-radius: 4px;
    border: none;
    background-color: #ffffff;
  }

  .popup-close {
    position:absolute;
    top: 7px;
    left: 743px;
    background: none;
  }

  .renewal-popup {
    background-color: rgba(0, 0, 0, 0.95);
    
    > .popup-content {
      background: none;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;

      > h1 {
        font-size: 48px;
        font-weight: 300;
        line-height: 1.33;
        letter-spacing: -4.5px;
        color: $sky-blue;
        margin-bottom: 42px;
      }

      > p {
        color: white;
        text-align: center;
        margin-bottom: 29px;
      }

      > .popup-btn {
        width: 300px;
        height: 56px;
        background-color: $purple;
        color: white;
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }
  }
}


