.themoin-company {
  @extend .themoin-landing;

  > div {
    @extend .themoin-container;
  }

  .company-slogan {
    margin: 45px 0;
    @media (max-width: $max-width) {
      margin: 28px 0;
      text-align: center;
    }

    h1:first-child {
      margin: 0;
    }

    h1:last-child {
      color: $deep-grey;
      margin-bottom: 0;
    }

    .blue {
      color: $dark-blue;
    }
  }

  .company-intro, .company-mission, .company-ci {
    margin-bottom: 100px;
    @media (max-width: $max-width) {
      margin-bottom: 40px;
    }

    > div {
      display: flex;
      flex-direction: row;
      @media (max-width: $max-width) {
        flex-direction: column;
        align-items: center;

        width: calc(100% - 32px);
        max-width: 380px;
      }
    }

    h2 {
      margin-top: 0;
      @media (max-width: $max-width) {
        margin-top: 26px;
        text-align: center;
      }
    }

    p {
      margin-top: 15px;
    }

    img {
      height: 26.63vw;
      @media (max-width: $max-width) {
        width: 100%;
        height: auto;
      }
      max-width: 380px;
      max-height: 253px;
      flex-shrink: 1;
    }

    .spacer {
      width: 8%;
      max-width: 100px;
      flex-shrink: 0;
    }

    > div {
      flex-grow: 1;
    }

  }

  .company-ci {
    @media (max-width: $max-width) {
      img.logo {
        margin-bottom: 26px;
      }
    }

    p {
      margin-top: 0;
      margin-bottom: 25px;
    }

    p > img.color {
      width: 20px;
      height: 20px;
      margin-right: 10px;
    }
  }

  .company-map {
    #company-map {
      width: 100%;
      height: 400px;
      @media (max-width: $max-width) {
        height: 240px;
      }

      background-color: $light-grey;
    }
  }

  .company-info {
    margin-bottom: 50px;

    > div {
      display: flex;
      flex-direction: column;
      @media (max-width: $max-width) {
        width: calc(100% - 32px);
      }
    }

    .row {
      display: flex;
      flex-direction: row;
      margin-bottom: 15px;

      &.no-margin {
        margin-bottom: 10px;
      }
    }

    .img {
      width: 24px;
      min-width: 24px;
      height: 24px;
      margin-left: 11px;
      margin-right: 10px;
      transform: translate3d(0, -6px, 0);
      background-repeat: no-repeat;
      background-position: 50% 50%;

      &.location { background-image: url(https://img.themoin.com/public/img/ic-location.png); }
      &.phone { background-image: url(https://img.themoin.com/public/img/ic-phone.png); }
      &.email { background-image: url(https://img.themoin.com/public/img/ic-email.png); }
    }

    p {
      margin-bottom: 0;
      line-height: 1.0;
    }

    .info-label {
      width: 56px;
      min-width: 56px;
    }

    .info-divider {
      width: 2px;
      height: 14px;
      background-color: $light-grey;
      margin-left: 7px;
      margin-right: 12px;
    }
  }

  .divider {
    width: 100%;
    height: 1px;
    margin-bottom: 44px;
    background-color: $light-grey;
    @media (max-width: $max-width) {
      display: none;
    }
  }
}


