.themoin-recipients {
  @extend .themoin-container;

  /* reset bootstrap */
  h1, h3, p {
    margin: 0;
    padding: 0;
  }

  > div {
    padding: 60px 0;
    @media (max-width: $max-width) {
      padding: 33px 0;
    }
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .recipients-add {
    width: 100%;
    height: 60px;
    margin: 50px 0 5px 0;
    @media (max-width: $max-width) {
      height: 80px;
      margin-top: 33px;
    }
    padding: 0 10px;
    text-align: center;
    border: solid 1px #dbdbdf;
    background-color: #f8f8f9;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .recipients {
    width: calc(100% - 20px);
    @media (max-width: $max-width) {
      max-width: 400px;
    }
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .recipient-card {
    width: 100%;
    margin: 5px 0;
    padding: 26px;
    @media (max-width: $max-width) {
      padding: 15px;
    }
    border: solid 1px #dbdbdf;

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

    .spacer {
      flex-grow: 1;
      @media (max-width: $max-width) {
        flex-grow: 0;
      }
    }

    .pic {
      text-align: center;
      padding-right: 40px;
      @media (max-width: $max-width) {
        display: none;
      }
    }

    .details {
      @media (max-width: $max-width) {
        min-width: 180px;
        flex-grow: 1;
      }

      display: flex;
      flex-direction: column;
      text-align: left;

      .moin-row {
        display: flex;
        flex-direction: row;

        p:first-child {
          width: 50px;
          min-width: 50px;
          @media (max-width: $max-width) {
            width: 40px;
            min-width: 40px;
          }
        }
      }

      .account {
        margin-top: 10px;
      }
    }

    .buttons {
      display: flex;
      flex-direction: row;
      @media (max-width: $max-width) {
        flex-direction: column-reverse;
      }

      justify-content: center;
      align-items: center;

      > button, a {
        width: 72px;
      }

      a {
        text-align: center;
        @media (max-width: $max-width) {
          height: 44px;
          line-height: 44px;
          margin-top: 15px;
        }
      }

      button {
        width: 72px;
        height: 44px;
        font-size: 14px;
        line-height: 1;
        letter-spacing: -1px;
      }

      .btn-del {
        font-size: 14px;
        font-weight: 400;
        letter-spacing: -1px;
        color: #a0a7b8;
        text-decoration: underline;
      }

      .btn-del:hover {
        color: #a0a7b8;
        text-decoration: underline;
      }

    }

    p {
      font-size: 14px;
      font-weight: 400;
      line-height: 1;
      letter-spacing: -1px;
      color: #a0a7b8;
      padding: 6px 0;
    }
  }
}


