.themoin-login {
  @extend .themoin-container;
  background-color: $back-grey;

  > div {
    padding: 70px 0;
    @media (max-width: $max-width) {
      padding: 0;
    }

    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .moin-login {
    width: calc(100% - 20px);
    max-width: 640px;
    background-color: #ffffff;
    border: solid 1px #e5e7ec;
    padding: 70px 80px 50px 80px;
    @media (max-width: $max-width) {
      max-width: 700px;
      width: 100vw;
      padding: 8px 6vw 54px 6vw;
      border: none;
    }

    text-align: center;
  }

  .logo {
    @media (max-width: $max-width) {
      display: none;
    }
  }

  .or {
    margin: 18px 0;
    height: 22px;
    line-height: 22px;
  }

  .btn-modal {
    @extend .hd-button;

    width: 100%;
    height: 56px;
    line-height: 1;
    color: #ffffff;
    margin: 0 auto
  }

  .btn-signup, .btn-login {
    @extend .btn-modal;

    background-color: #292767;
    margin-top: 34px;
    @media (max-width: $max-width) {
      margin-top: 20px;
    }
  }

  .btn-facebook {
    @extend .btn-modal;

    background-color: #4262a3;
  }

  .btn-naver {
    @extend .btn-modal;

    margin-top: 12px;
    background-color: #56bc37;
  }

  .reset-password-popup {
    width: calc(100% - 30px);
    max-width: 460px;
    border-radius: 3px;

    padding: 50px 60px;
    display: flex;
    flex-direction: column;
    align-items: center;

    form {
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;

      .moin-input-group {
        width: 100%;
      }

      input[type="submit"] {
        margin: 20px 0;
      }

      a {
        margin-bottom: 20px;
      }
    }

    .modal-btn {
      width: 100%;
      height: 56px;
      border: none;
    }
  }

  .modal {
    .modal-content {
      width: 95vw;
      max-width: 460px;
      padding: 50px 60px;
      text-align: center;
    }

    .modal-close.close {
      top: 7px;
      left: 483px;
    }
  }
}


