.remit-review {
  .user-sendlist-ic {
    width: 5.6px;
    height: 8px;
    margin: 0px 16px;
  }

  > div {
    max-width: 620px!important;
  }

  .mobile {
    display: none;
  }

  .review-amount, .review-sender, .review-receiver {
    border: 1px solid $light-grey;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 35px 0;

    > :first-child {
      text-align: center;
      width: 140px;
    }
    > :nth-child(2) {
      flex-grow: 1;
      margin: 0 20px;
      display: flex;
      align-items: center ;
    }
  }

  .review-sender, .review-receiver {
    border-top: none;
  }

  @media (max-width: $max-width) {
    .review-amount, .review-sender, .review-receiver {
      flex-direction: column;
      padding: 20px;
    }

    .review-sender, .review-receiver {
      > :nth-child(2) {
        width: 100%;
        margin: 0;
        margin-top: 15px;
      }
    }

    .review-icon > img {
      display: none;
    }

    .mobile {
      display: block;
    }
  }
}


