.themoin-remit-component {
  @extend .themoin-container;
  padding: 20px 0 45px 0;
  @media (max-width: $max-width) {
    padding: 15px 0 0 0;
  }

  > .moin-amount {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    @media (max-width: $max-width) {
      flex-direction: column;
    }
  }

  .mobile {
    display: none;
  }
  @media (max-width: $max-width) {
    .mobile {
      display: block;
    }
  }

  .moin-event-amount {
    margin-top: 10px;

    p {
      text-align: center;
      margin: 0;
    }

    &.moin-hidden {
      height: 0;
    }

    @media (max-width: $max-width) {
      margin: 0;
    }
  }

  .moin-amount {
    margin-top: 30px;
    @media (max-width: $max-width) {
      margin-top: 15px;
    }
  }

  .amount {
    max-width: 460px;
    width: 100%;
    margin-right: 20px;
    @media(max-width: $max-width) {
      margin: 0 0 20px 0;
    }

    .index-send, .index-receive {
      display: flex;
      flex-direction: row;
    }

    .index-amount {
      border: solid 1px #dbdbdf;
      background-color: white;
      flex: 1;
      height: 83px;
    }

    .index-receive .index-amount {
      border-top: none;
    }

    .index-amount-label, .user-amount-label{
      height:31px;
      padding: 17px 0 0 24px;
      line-height: 1;

      @media(max-width: $max-width) {
        padding: 15px 0 0 16px;
      }
    }

    .index-amount-input:focus, .user-amount-input:focus {
      border: none;
      box-shadow: none;
      outline: 0 none;
    }

    .index-unit, .user-unit, .moin-unit{
      width: 140px;
      height: 83px;
      padding: 16px 0 0 20px;
      border: solid 1px #dbdbdf;
      border-left: none;
      background-color: white;
    }

    .index-receive .index-unit {
      border-top: none;
    }

    .index-unit>a, .user-unit>a, .moin-unit>a {
      width:100%;
      height: 100%;
      display: block;
    }

    .index-unit-country, .user-unit-country, .moin-unit-country {
      padding-bottom: 14px;
      line-height: 1;
    }

    @media (max-width: $max-width) {
      .index-unit, .user-unit {
        height: 74px;
        width: 113px;
      }

      .index-amount {
        height: 74px;
      }
    }
  }

  .index-amount-input, .moin-amount-input {
    width: 100%;
    margin-top: 7px;
    font-size: 20px;
    line-height: 0.9;
    letter-spacing: -0.5px;
    color: $dark-blue;
    height: 27px;
    padding: 0 24px 0 24px;
    border: none;

    @media(max-width: $max-width) {
      margin-top: 4px;
      font-size: 18px;
      padding: 0 16px 0 16px;
    }
  }
  
  .moin-unit-text, .index-unit-text {
    font-size: 20px;
    line-height: 0.9;
    letter-spacing: -0.5px;
    @media(max-width: $max-width) {
      font-size: 18px;
    }
  }

  .index-unit-toggle, .user-unit-toggle, .moin-unit-toggle {
    margin-top: 23px;
    margin-right: 19px;
    @media (max-width: $max-width) {
      margin-top: 17px;
      margin-right: 14px;
    }
  }

  .money-graph {
    max-width: 460px;
    width: 100%;
    height: 248px;
    padding: 0 38px;
    background-color: #ffffff;
    border: solid 1px #dbdbdf;

    @media (max-width: $max-width) {
      margin-top: 33px;
      padding: 0 22px;
      height:190px;

      .index-money-title {
        margin-top: 23px;
        margin-bottom: 11px;
      }

      .index-money-bank, .index-money-moin, .index-money-moin-event {
        font-size: 13px;
        margin-top: 15px;
      }

      .index-money-bar {
        margin-top: 4px;
        margin-left: 0;
      }

      .index-money-rank {
        height: 120px;
      }
    }
  }

  .index-money-title {
    text-align: center;
    margin: 33px auto 0 auto;
  }

  .index-money-bank, .index-money-moin, .index-money-moin-event {
    margin-top:25px;
    display: flex;
    flex-direction: row;
  }

  .index-money-rank {
    margin-top: 9px;
    height: 156px;
    overflow: hidden;
    position: relative;
  }

  .index-money-bar {
    height: 6px;
    margin-left: 8px;
    margin-top: 4px;
    -webkit-transition: width 1s ease;
    transition: width 1s ease;
    width: 0;
    flex: 1 0;
  }

  h1 {
    padding-bottom: 20px;
    margin: 0;
  }

  .form-amount {
    .moin-event-amount {
      margin: 0;
      padding-bottom: 5px;
    }
    table {
      margin-top: 20px;
    }

    tbody {
      tr {
        td:first-child {
          height: 85px;
          padding: 0 23px;
          p {
            margin: 0;
            line-height: 1;
          }

          .moin-amount-input {
            padding: 0px;
          }
        }
        td:last-child {
          width: 140px;
          padding-left: 23px;
          border-left: solid 1px #dbdbdf;

          .moin-body {
            line-height: 1;
          }

          .moin-unit-text {
            margin-top: 14px;
            @media(max-width: $max-width) {
              margin-top: 11px;
            }
          }
        }

        @media (max-width: $max-width) {
          width: 100%;

          td:first-child {
            padding-left: 12px;
            height: 74px;
          }

          td:last-child {
            padding-left: 12px;
            height: 74px;
            width: 113px;
          }
        }
      }
    }
  }

  .form-graph {
    display: flex;
    max-width: 660px;
    .index-money-rank {
      flex: 1;
      margin-top: 7px;
      padding-right: 10px;
    }

    .button {
      width: 140px;
      button {
        width: 100%;
        &:last-child {
          margin-top: 10px;
        }
      }
    }

    @media (max-width: $max-width) {
      display: block;

      .index-money-rank {
        height: 138px;
        width: 100%;
        padding-top: 18px;
      }

      .index-money-title {
        margin-top: 23px;
        margin-bottom: 11px;
      }

      .index-money-bank, .index-money-moin, .index-money-moin-event {
        font-size: 13px;
        margin-top: 15px;
      }

      .index-money-bar {
        margin-top: 3px;
      }

      .button {
        width: 100%;
        display: flex;
        margin-top: 30px;
        button {
          flex: 1;
          &:first-child {
            margin-right: 16px;
          }
          &:last-child {
            margin-top: 0;
          }
        }
      }
    }
  }

  .index-send-btn {
    width: 100%;
    height: 70px;
    background-color: #5451a6;
    text-align: center;
    line-height: 1;
    color: #ffffff;
    margin-top: 11px;
    @media(max-width: $max-width) {
      margin-top: 19px;
      height: 56px;
    }
  }

  .index-money-moin .index-money-name, .index-money-moin .index-money-amount, .index-money-moin .index-money-unit{
    color: $sky-blue!important;
  }

  .index-money-moin .index-money-bar > div{
    background-color: $sky-blue!important;
  }

  .index-money-moin-event .index-money-name, .index-money-moin-event .index-money-amount, .index-money-moin-event .index-money-unit {
    color: $dark-blue!important;
  }

  .index-money-moin-event .index-money-bar > div{
    background-color: $dark-blue!important;
  }

  .index-money-bank .index-money-bar > div {
    background-color: $deep-grey;
  }

  .index-money-name {
    width: 55px;
    color: $deep-grey;
    height: 14px;
    line-height: 1;
  }

  .index-money-amount {
    margin-left: 13px;
    color: $deep-grey;
    height: 14px;
    line-height: 1;
    @media(max-width: $max-width) {
      margin-left: 6px;
    }
  }

  .index-money-unit {
    margin-left: 3px;
    color: $deep-grey;
    height: 14px;
    line-height: 1;
  }
}



