.moin-input {
  > label {
    font-size: 14px;
    line-height: 1;
    letter-spacing: -1px;
    font-weight: normal;
    color: #a0a7b8;
    margin: 0;
    vertical-align: bottom;
  }

  > input {
    border: none;
    height: 40px;
    font-size: 18px;
    line-height: 130%;
    letter-spacing: -1px;
    color: #292767;
    width: 100%;
    vertical-align: middle;
    border-bottom: solid 1px #bac0cd;
    border-radius: 0;
    padding: 0;
  }

  > input:focus {
    box-shadow: none;
    outline: 0 none;
    border-bottom: solid 1px #292767;
  }
}

.moin-input-error {
  border-bottom: solid 1px #EE1717 !important;
}


