.themoin-event-univ-jpy {
  @extend .reset-bootstrap;

  .mobile-line-break {
    display: none;
    @media (max-width: $max-width) {
      display: inherit;
    }
  }

  @media (max-width: $max-width) {
    .desktop-line-break {
      display: none;
    }
  }

  margin-top: -75px;
  @media (max-width: $max-width) {
    margin-top: -52px;
  }

  background-image: url('https://img.themoin.com/public/img/img-event.svg');
  background-repeat: no-repeat;
  background-position: top right;
  @media (max-width: 1200px) {
    background-position: top left;
    background-position-x: calc(1200px - 303px);
  }

  > div {
    @extend .themoin-container;
    border: solid 1px $line-grey;
    border-width: 0 0 1px 0;

    > div {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  }

  > div:last-child {
    border: none;
  }

  .section-title {
    padding: 55px 0;
    @media (max-width: $max-width) {
      padding: 24px 0 22px 0;
    }

    img {
      width: 120px;
      height: 38px;
      margin-bottom: 40px;
      @media (max-width: $max-width) {
        width: 69px;
        height: 22px;
        margin-bottom: 22px;
      }
    }

    h1 {
      font-weight: 100;
      letter-spacing: -4px;
      line-height: 1.0;
      font-size: 42px;
      font-weight: 300;
      @media (max-width: $max-width) {
        font-size: 16px;
        letter-spacing: -0.5px;
        font-weight: 400;
      }
    }

    .pre-title {
      color: $deep-grey;
      margin-bottom: 28px;
      font-weight: 100;
      @media (max-width: $max-width) {
        color: $grey;
        font-weight: 400;
        margin-bottom: 12px;
      }
    }
  }

  .section-intro,
  .section-howto,
  .section-compare,
  .section-faq,
  .section-process,
  .section-verifyemail {
    @media (max-width: $max-width) {
      h1 {
        font-size: 16px;
        font-weight: 400;
        text-align: center;
      }
    }
  }

  .section-intro {
    padding: 60px 0;
    @media (max-width: $max-width) {
      padding: 30px 0 38px 0;
    }

    h1 {
      margin-bottom: 18px;
      @media (max-width: $max-width) {
        line-height: 1.44;
      }
    }

    p {
      text-align: center;
      margin-bottom: 38px;
      font-size: 18px;
      line-height: 1.72;
      letter-spacing: -0.5px;
      @media (max-width: $max-width) {
        max-width: 380px;
        font-size: 13px;
        line-height: 1.69;
        letter-spacing: -0.4px;
        margin-bottom: 30px;
      }
    }

    .intro-vs {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      color: $dark-blue;
      width: 500px;
      @media (max-width: $max-width) {
        width: 150px;
      }

      > div {
        font-size: 42px;
        font-weight: 300;
        @media (max-width: $max-width) {
          letter-spacing: -1px;
          font-size: 26px;
          font-weight: 400;
        }
        line-height: 1.0;
      }


      .label-moin-normal {
        text-decoration: line-through;
        color: $grey;
      }

      @media (max-width: $max-width) {
        img:first-child, img:last-child {
          display: none;
        }

        img {
          width: 8px;
          height: 10px;
        }
      }
    }
  }

  .section-howto {
    padding: 60px 0 83px 0;
    @media (max-width: $max-width) {
      padding: 30px 0 10px 0;
    }

    h1 {
      margin-bottom: 35px;
      @media (max-width: $max-width) {
        margin-bottom: 0;
      }
    }
 
    img.desktop {
      width: 100%;
      max-width: 940px;
      @media (max-width: $max-width) {
        display: none;
      }
    }

    .mobile {
      display: none;
      @media (max-width: $max-width) {
        display: flex;
      }

      flex-direction: column;
      align-items: center;

      p {
        margin-bottom: 20px;
      }
    }
  }

  .section-compare {
    padding: 61px 0 55px 0;
    @media (max-width: $max-width) {
      padding: 30px 0;
    }

    h1 {
      margin-bottom: 41px;
      @media (max-width: $max-width) {
        margin-bottom: 31px;
      }
    }

    img.desktop {
      width: 100%;
      max-width: 940px;
      @media (max-width: $max-width) {
        display: none;
      }
    }

    img.mobile {
      display: none;
      @media (max-width: $max-width) {
        display: block;
      }
    }
  }

  .section-faq {
    padding: 60px 0 80px 0;
    @media (max-width: $max-width) {
      padding: 30px 0 0 0;
    }

    .blue {
      color: #1f1a54;
    }

    .item-container {
      display: flex;
      flex-direction: row;
      align-items: flex-start;
      @media (max-width: $max-width) {
        flex-direction: column;
        align-items: center;
      }
 
      justify-content: space-between;
      align-content: space-between;
    }

    .item-container:first-child {
      min-height: 290px;
      @media (max-width: $max-width) {
        min-height: inherit;
      }
    }

    .item {
      max-width: 450px;
      flex-basis: 450px;
      flex-shrink: 1;
      @media (max-width: $max-width) {
        min-width: inherit;
        flex-basis: inherit;
      }
    }

    .item:first-child {
      margin-right: 20px;
      @media (max-width: $max-width) {
        margin-right: 0;
      }
    }

    @media (max-width: $max-width) {
      .item-container:last-child {
        width: 100%;

        .item {
          border: solid 1px $line-grey;
          border-width: 1px 0 0 0;
          width: 100%;
          padding: 30px 16px 0 16px;

          display: flex;
          flex-direction: column;
          align-items: center;
        }
      }
    }

    img {
      width: 100%;
    }

    @media (max-width: $max-width) {
      img.desktop {
        display: none;
      }
    }

    img.mobile {
      margin-bottom: 48px;
      display: none;
      @media (max-width: $max-width) {
        display: inherit;
      }
    }

    h1 {
      margin-bottom: 24px;
      @media (max-width: $max-width) {
        margin-bottom: 22px;
      }
    }

    p {
      max-width: 380px;
      @media (max-width: $max-width) {
        margin-bottom: 30px;
      }
    }
  }

  .section-process {
    @media (max-width: $max-width) {
      display: none;
    }

    padding: 60px 0 53px 0;

    .process {
      margin: 0;
      padding: 0;
    }

    .process > div {
      width: 100%;
      display: flex;
      align-items: center;
    }

    h1 {
      width: 100%;
      text-align: left;
      padding: 0;
      margin-bottom: 85px;
    }
  }

  .section-step {
    @media (max-width: $max-width) {
      display: none;
    }

    padding: 60px 0 33px 0;

    h1 {
      width: 100%;
      text-align: left;
      padding: 0;
      margin-bottom: 80px;
    }
  }

  .section-verifyemail {
    background-color: $pale;
    padding: 46px 0;
    @media (max-width: $max-width) {
      padding: 24px 0 29px 0;
    }

    form {
      @media (max-width: $max-width) {
        width: 100%;
        max-width: 360px;
      }

      margin-top: 30px;
      display: flex;
      flex-direction: row;
      @media (max-width: $max-width) {
        flex-direction: column;
      }

      justify-content: center;
    }

    input {
      height: 50px;
    }

    .input-email {
      width: 360px;
      margin-right: 5px;
      @media (max-width: $max-width) {
        width: 100%;
        margin-right: 0;
        margin-bottom: 10px;
      }
    }

    input[type='text'] {
      padding-left: 20px;
      border: solid 1px $grey;
      @media (max-width: $max-width) {
        font-size: 16px;
      }
    }

    input[type='submit'] {
      appearance: none;
      width: 120px;
      @media (max-width: $max-width) {
        width: 100%;
      }
      height: 50px;
      background-color: #5351a6;
      color: $white;
      font-size: 14px;
      border: none;
    }
  }
}


