.themoin-remit-form {
  /* reset default body padding */
  margin-top: -76px;

  ul, h3, p, label { // reset bootstrap
    margin: 0;
  }

  .section {
    position: absolute;
    width: 100vw;
    height: 100vh;
    overflow: hidden;

    > div {
      width: 100%;
      height: 100%;
      padding: 80px 0;
      display: flex;
      position: absolute;
      overflow-y: auto;

      > div {
        max-width: 640px;
        width: 100%;
        @media (max-width: $max-width) {
          width: calc(100% - 32px);
        }

        margin: auto;
        overflow-x: visible;
      }

      &.hidden-left {
        opacity: 0;
        transform: translate3d(-100%, 0, 0);
      }

      &.hidden-right {
        opacity: 0;
        transform: translate3d(100%, 0, 0);
      }

      &.animate {
        transition: transform 0.4s ease-in-out, opacity 0.4s ease-in-out;
      }
    }
  }

  .remit-header {
    position: fixed;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    top: 0;
    left: 0;
    height: 75px;
    z-index: 1030;
    width: 100%;
    padding: 0 10px;
    background-color: $white-90;

    a {
      margin-right: 30px;
      text-decoration: underline;
    }
  }

  .remit-footer {
    position: fixed;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    bottom: 0;
    left: 0;
    height: 74px;
    z-index: 1030;
    width: 100%;
    border-top: solid 1px #dbdbdf;
    background-color: $white-90;
    padding: 0 10px;

    > div {
      width: 100%;
      height: 100%;
      max-width: 620px;
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;
    }

    ul {
      flex: 1;
      max-width: 640px;

      display: flex;
      flex-direction: row;
      align-items: center;
      order: -1;
      justify-content: flex-start;
      @media (max-width: $max-width) {
        order: 0;
        justify-content: center;
      }
    }

    button:last-child {
      margin-left: 10px;
      @media (max-width: $max-width) {
        margin-left: 0;
      }
    }

    button {
      width: 100px;
      height: 44px;
      background-color: white;
      border: solid 1px $grey;
      font-size: 14px;
      line-height: 1;
      letter-spacing: -0.5px;
      text-align: center;
      color: $deep-grey;

      &.invisible {
        visibility: hidden;
      }

      &.disabled {
        opacity: 0.5;
      }

      &.next {
        background-color: $purple;
        color: white;
        border: none;
      }

      &.submit {
        background-color: $sky-blue;
        color: white;
        border: none;
      }

      @media (max-width: $max-width) {
        width: 60px;
        height: 36px;
      }

      .desktop {
        @media (max-width: $max-width) {
          display: none;
        }
      }
    }
  }

  .remit-bar {
    width: 30px;
    height: 30px;
    background-color: #ffffff;
    border: solid 1px $light-grey;
    font-size: 14px;
    line-height: 28px;
    letter-spacing: -1px;
    text-align: center;
    color: $deep-grey;
    margin-right: 5px;
    @media (max-width: $max-width) {
      width: 28px;
      height: 8px;
      margin-right: 3px;
    }

    .desktop {
      @media (max-width: $max-width) {
        display: none;
      }
    }
  }

  .remit-bar-active {
    background-color: $grey!important;
    border: solid 1px $grey!important;
    color: white!important;
  }

  .remit-head {
    font-weight: 400;
    font-size: 22px;
    line-height: 1;
    letter-spacing: -1.1px;
    color: #292767;
    background-color: transparent !important;
    border: none !important;
  }

  .RemitFormPhotoUpload {
    > .row {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;

      > div {
        width: 320px;
        padding: 0 10px;
      }
    }
  }

  .remit-saved-list {
    border: solid 1px #dbdbdf;
    padding: 20px 32px;
    display: flex;
    width: 100%;

    &:hover {
      background-color: #F3F4F7;

    }

    > table {
      height: 70px;
      flex: 1;
    }

    @media (max-width: $max-width) {
      padding: 10px 20px;
    }
  }

  .remit-saved-table {
    width: 100%;
    border:solid 1px #dbdbdf;
    display: flex;

    > img {
      margin: 32px;
    }

    > table {
      flex: 1;
    }
  }

  @media (max-width: $max-width) {
    .desktop {
      display: none;
    }
  }
}


