.themoin-main-remititem {
  /* reset bootstrap */
  h1, h2, h3, p { margin: 0; }

  margin: 5px 0;
  width: calc(100vw - 32px);
  max-width: 960px;

  border: solid 1px #dbdbdf;
  overflow: hidden;
  cursor: pointer;

  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;

  &:hover {
    background-color: #F3F4F7;
  }

  .simple {
    display: flex;

    &.simple-hidden {
      display: none;
      @media (max-width: $max-width) {
        display: flex;
      }
    }

    flex-direction: row;
    align-items: center;
    @media (max-width: $max-width) {
      flex-direction: column;
      align-items: flex-start;
      justify-content: center;
    }

    width: 100%;
    height: 100px;

    .username {
      margin-bottom: 12px;
      @media (max-width: $max-width) {
        margin-bottom: 5px;
      }
    }

    &-nametime {
      width: 150px;
      margin: 5px 30px 0 22px;
      @media (max-width: $max-width) {
        margin: 0 0 0 22px;
      }

      > .create-time {
        @media (max-width: $max-width) {
          display: none;
        }
      }
    }

    &-spacer {
      width: 1px;
      height: 60px;
      margin: 0;
      background-color: $light-grey;
      @media (max-width: $max-width) {
        display: none;
      }
    }

    &-amount {
      flex: 1 1 175px;
      margin: 0 22px;
      @media (max-width: $max-width) {
        flex: 0 1 auto;
        margin: 0 0 5px 22px;
      }

      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      align-items: center;

      h3 {
        height: 30px;
        line-height: 30px;
      }

      .arrow {
        margin: 0 10px;
        width: 10px;
        height: 30px;
        background-image: url(https://img.themoin.com/public/img/ic-next-p.png);
        background-position: 50% 50%;
        background-repeat: no-repeat;
      }

      .unit {
        color: $grey;
      }
    }

    /* temp below */
    button, .user-sendlist-state-cancel, .more {
      text-align: center;
      @media (max-width: $max-width) {
        display: none;
      }
    }

    .user-sendlist-amount {
      @media (max-width: $max-width) {
        margin-left: 22px;
        height: 30px;
        flex-grow: 0;
      }
    }

    .user-sendlist-status {
      @media (max-width: $max-width) {
        margin-left: 22px;
        justify-content: flex-start;
        .user-sendlist-state, .user-sendlist-state-text {
          margin: 0;
        }
      }
    }
  }

  .detail {
    width: 100%;
    height: 274px;
    &.detail-hidden {
      display: none;
    }

    @media (max-width: $max-width) {
      display: none;
    }
  }

  .title-index {
    font-weight: 100;
    font-size: 42px;
    line-height: 1;
    color: #dbdbdf;
    margin: 0 0 0 18px;
    width: 72px;

    display: flex;
    align-items: center;
    justify-content: center;
    
    @media (max-width: 850px) {
      display: none;
    }
  }

  .username {
    color: $dark-blue;
  }

  .create-time {
    color: $deep-grey;
  }

  /* detail-popup */
  .detail-popup {
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    z-index: 2000;
    background-color: rgba(0, 0, 0, 0.5);

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 5px 16px;

    > div, a {
      width: 100%;
      max-width: 550px;
      border-radius: 3px;
    }

    .popup-content {
      margin-bottom: 10px;
      display: flex;
      flex-direction: column;
      align-items: center;
      background-color: $white;
      overflow-y: auto;

      .accountname {
        margin-top: 24px;
        margin-bottom: 24px;
      }

      .caption {
        margin-bottom: 10px;
        width: calc(100% - 20px);
        max-width: 400px;
      }

      .status {
        margin-top: 10px;
        margin-bottom: 10px;
      }

      .modify-time {
        text-align: center;
      }

      .divider {
        width: 100%;
        height: 1px;
        background-color: $line-grey;
        margin-top: 10px;
        margin-bottom: 10px;
      }

      .buttons {
        width: 100%;
        margin-top: 10px;
        display: flex;
        flex-direction: row;
        border: solid 1px $line-grey;
        border-width: 1px 0 0 0;

        a {
          flex-grow: 1;
          text-align: center;
          height: 50px;
          line-height: 50px;

          &:nth-child(2) {
            border-top-left-radius: 0;
            border: solid 1px $line-grey;
            border-width: 0 0 0 1px;
          }
        }

        .modify {
          color: $dark-blue;
        }
      }

      /* legacy */
      .user-sendlistdetail-amount {
        margin-top: 10px;
        margin-bottom: 10px;
      }
    }

    .popup-close {
      height: 54px;
      background-color: $line-grey;

      display: flex;
      align-items: center;
      justify-content: center;
      color: $dark-blue;

      &:hover {
        opacity: 0.9;
      }
    }
  }


  /* legacy */
  .user-sendlist-amount {
    margin-left: 34px;
    margin-right: 15px;
    flex-grow: 1;
    flex-shrink: 1;
  }

  .user-sendlist-status {
    margin: 0 10px;
  }
}


