.themoin-landing-step {
  width: 100%;
  max-width: 860px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;

  > div {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 234px;
    margin: 0 20px 50px 20px;

    img {
      width: 100%;
      margin-bottom: 33px;
    }

    div {
      margin-bottom: 10px;
      line-height: 1.0;
      font-size: 18px;
      color: $dark-blue;
    }
  }
}


