.themoin-recipients-update {
  /* reset bootstrap */
  h1 {
    margin: 0;
  }

  @extend .themoin-container;
  padding: 55px 10px;
  @media (max-width: $max-width) {
    padding: 30px 10px;
  }

  > div {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  form {
    width: 100%;
    max-width: 680px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .form-contents {
    display: flex;
    width: 100%;
    flex-direction: column;
  }

  .top-spacer {
    margin-top: 45px;
    @media (max-width: $max-width) {
      margin-top: 15px;
    }
  }

  .form-row {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;

    > div {
      margin-top: 25px;
      @media (max-width: $max-width) {
        margin-top: 16px;
      }
      padding-left: 10px;
      padding-right: 10px;
      flex-grow: 1;
      width: 100%;
    }

    &.col-2 > div{
      width: 40%;
      min-width: 135px;
      flex: 1 1 135px;
    }
  }

  /* form components */
  .btn-searchbank {
    color: $purple;
    border: solid 1px $purple;
    background-color: $white;
    width: 100%;
    height: 60px;
  }
}


