.themoin-landing {
  .intro, .description, .pros, .video {
    @extend .themoin-container;
  }

  .landing-pending {
    z-index: 1020;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: $white;
  }

  .moin-row {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;

    > div {
      padding: 0 10px;
    }
  }

  .intro {
    > div.desktop {
      padding: 50px 0 100px 0;
      display: flex;
      @media (max-width: $max-width) {
        display: none;
      }
      flex-direction: row;

      .col-left {
        width: 59.5744680851064%;
        padding: 0 10px;

        .moin-header {
          padding-top: 15px;
          @media (max-width: 850px) {
            font-size: 30px;
          }
        }

        > .moin-header:first-child {
          padding-bottom: 2px;
        }

        p {
          margin-top: 23px;
          font-size: 16px;
          font-weight: normal;
          font-style: normal;
          font-stretch: normal;
          line-height: 1.62;
          letter-spacing: -1px;
          color: $deep-grey;
        }
      }
  
      .col-right {
        align-self: center;
        width: 40.4255319148936%;
        text-align: center;
        img {
          max-width: 328px;
          width: 100%;
          height: auto;
        }
      }
    }

    > div.mobile {
      padding: 27px 0;
      display: none;

      @media (max-width: $max-width) {
        display: flex;
        flex-direction: column;
        align-items: center;
      }

      > .mobile-header:first-child {
        padding-bottom: 16px;
      }
    }
  }

  .description {
    > div {
      padding: 60px 0 90px 0;
      display: flex;
      @media (max-width: $max-width) {
        display: none;
      }

      flex-direction: column;
      justify-content: center;
    }

    .moin-row.images img {
      max-width: 460px;
      width: 100%;
      height: auto;
    }

    .moin-row.details {
      .moin-panel {
        flex: 1 1 0px;

        &.left { padding-left: 30px; }
        &.right { padding-right: 30px; }
      }

      .moin-arrow {
        width: 60px;
      }
    }
  }

  .pros {
    background-color: $pale;
    padding-bottom: 77px;
    @media (max-width: $max-width) {
      padding-bottom: 12px;
    }
    > div {
      text-align: center;
    }

    h1 {
      padding: 61px 0 39px 0;
      text-align: center;
      @media (max-width: $max-width) {
        padding: 16px 0 22px;
      }
    }

    .moin-row {
      flex-wrap: wrap;
    }

    .card-pros {
      min-width: 300px;
      width: 300px;
      margin: 10px;
      padding: 0;
      @media (max-width: $max-width) {
        min-width: 288px;
        width: 288px;
        margin: 3px;
      }

      .moin-index-box {
        height: 200px;
        background-color: #ffffff;
        border: solid 1px #e5e7ec;
        padding-top: 10px;
        text-align: center;
        @media (max-width: $max-width) {
          height: 160px;
          padding-top: 0;
        }

        p {
          padding: 24px 0 18px;
          @media (max-width: $max-width) {
            padding: 16px 0 12px;
            font-size: 13px;
            line-height: 1.69;
            letter-spacing: -0.3px;
          }
        }

        a {
          @media (max-width: $max-width) {
            font-size: 13px;
            line-height: 1.0;
            letter-spacing: -0.5px;
          }
        }
      }
    }
  }

  .process {
    justify-content: center;
    padding: 60px 0 83px 0;
    @media (max-width: $max-width) {
      padding: 39px 0 19px;
    }

    h1 {
      padding: 36px 0;
      @media (max-width: $max-width) {
        padding: 0 0 22px 0;
      }
      text-align: center;
      color: $dark-blue;
    }

    .moin-row {
      align-items: center;
      @media (max-width: $max-width) {
        flex-direction: column;
        align-items: center;
      }

      > div {
        padding: 0;
        &:first-child { padding-left: 10px; }
        &:last-child { padding-left: 10px; }
        @media (max-width: $max-width) {
          padding: 0;
        }
      }
    }

    .content-img {
      width: 100px;
      height: 100px;
    }

    .spacer {
      width: 20px;
      height: 30px;
      @media (max-width: $max-width) {
        display: none;
      }
    }

    .card-prize {
      width: 300px;
      min-width: 210px;
      height: 240px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: flex-start;
      @media (max-width: $max-width) {
        height: 197px;
      }
      h3 {
        color: $dark-blue;
      }
    }

    .index-contents {
      text-align: center;
      h3 {
        padding: 16px 0 22px 0;
        @media (max-width: $max-width) {
          padding: 5px 0 9px 0;
        }
      }
    }
  }

  .video {
    padding: 53px 0 93px 0;
    background-color: $pale;
    @media (max-width: $max-width) {
      padding: 36px 0 45px 0;
    }

    h1 {
      margin-bottom: 30px;
    }

    .title {
      padding-bottom: 37px;
      @media (max-width: $max-width) {
        padding-bottom: 17px;
      }
      text-align: center;
      color: $dark-blue;
    }

    .embed {
      position: relative;
      margin-left: 15px;
      padding-bottom: calc((100% - 30px)*0.5634); /* 16:9 */
      width: calc(100% - 30px);
      height: 0;
    }

    .link {
      padding-top:58px;
      @media (max-width: $max-width) {
        padding-top: 27px;
      }
      text-align:center;
      text-decoration: underline;
    }

    iframe {
      position: absolute;
      max-width: 940px;
      max-height: 529px;
      width: 100%;
      height: 100%;
    }
  }

  .funds {
    @media (max-width: $max-width) {
      display: none;
    }

    padding: 90px 10px;
    text-align: center;

    img {
      max-width: 940px;
      width: 100%;
      height: auto;
    }
  }

  .prize {
    padding: 60px 0;
    text-align: center;
    p {
      padding-top: 30px;
    }
    img {
      margin-top: 13px;
    }
    div {
      padding-top: 15px;
    }

    @media (max-width: $max-width) {
      img {
        width: 100px;
        height: 111px;
      }
      p {
        padding-top: 26px;
      }
      div {
        padding-top: 8px;
      }
      padding: 29px 0 54px 0;
    }


  }
  .mobileLink {
    padding: 70px 0;
    background-color: $pale;

    >div {
      display: flex;
      flex-direction: row;
      justify-content: center;
    }

    .btn-android, .btn-ios {
      width: 200px;
      height: 60px;
      border-radius: 3px;
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;

      >div {
        padding-left: 13px;
      }
    }

    .btn-android {
      background-color: $purple;
      margin-right: 35px;
      >div {
        color: white;
      }
    }

    .btn-ios {
      background-color: $line-grey;
      >div {
        color: $deep-grey;
      }
    }
  }
}


