    font-size: 18px;
    line-height: 48px;
    letter-spacing: -1px;
    color: #bac0cd !important;
    vertical-align: middle;
  line-height: 100%;
} /* ie */
input:-moz-placeholder {
    font-weight: 400;
    font-size: 18px;
    line-height: 48px;
    letter-spacing: -1px;
    color: #bac0cd;
    vertical-align: middle;
