.themoin-header {
  @extend .themoin-container;

  position: fixed;
  z-index: 1030;
  top: 0;
  left: 0;

  border-width: 0 0 1px 0;
  border-color: #e7e7e7;
  border-style: solid;
  background-color: rgba(255, 255, 255, 0.9);

  > div {
    height: 75px;
    @media (max-width: $max-width) {
      height: 52px;
    }

    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    align-items: center;
    justify-content: center;
  }

  .logo:hover { opacity: 1; }
  .logo img {
    height: 27px;
    @media (max-width: $max-width) {
      height: 17px;
    }
  }

  .spacer {
    flex-grow: 1;
  }

  /* bootstrap.css has pre-defined hidden class
    .hidden {
      display: none !important;
    }
  */

  .menu {
    width: 24px;
    height: 24px;
    margin-right: 13px;

    display: none;
    @media (max-width: $max-width) {
      display: inline;
    }

    &:hover {
      opacity: 0.8;
    }

    > div {
      background-color: $dark-blue;
      height: 1px;
      width: 18px;
      border: none;
      transition: transform 0.5s;
      transform-origin: 50% 50%;
    }

    .line1 { transform: translate3d(3px, 8px, 0); }
    .line2 { transform: translate3d(3px, 17px, 0); }

    &.open {
      .line1 { transform: translate3d(3px, 13px, 0) rotate3d(0, 0, 1, 135deg); }
      .line2 { transform: translate3d(3px, 12px, 0) rotate3d(0, 0, 1, 45deg); }
    }
  }


  ul {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-top: 0;
    margin-bottom: 0;
    margin-right: -10px;
    @media (max-width: $max-width) {
      margin-right: 0;
    }

    li {
      list-style-type: none;
      font-size: 14px;
      font-weight: 400;
      line-height: 1;
      letter-spacing: -1px;
      text-align: center;
      padding: 30px 22px;
      @media (max-width: $max-width) {
        padding: 15px 15px 10px 15px;
      }

      a {
        color: #292767;
        text-decoration: none;
      }
    }

    li.mobile {
      display: none;
    }
  }

  .verify {
    margin-left: 22px;
    width: 80px;
    height: 24px;

    @media (max-width: $max-width) {
      display: none;
    }
  }

  @media (max-width: $max-width) {
    ul {
      display: block;
      position: fixed;
      top: 52px;
      left: 0;
      width: 100vw;
      padding-bottom: 20px;
      border-width: 0 0 1px 0;
      border-color: #e7e7e7;
      border-style: solid;
      background-color: rgba(255, 255, 255, 0.9);

      z-index: 10;
      opacity: 0;
      transform: scale3d(1, 0, 1);
      transform-origin: 50% 0%;
      transition: transform 0.5s;
      transition-delay: 0.3s;

      li {
        opacity: 0;
        transition: opacity 0.3s;
        transition-delay: 0s;
      }
      li.mobile {
        display: block;
      }
    }

    ul.open {
      opacity: 1;
      transform: scale3d(1, 1, 1);
      transition-delay: 0s;

      li {
        opacity: 1;
        transition-delay: 0.5s;
      }
    }
  }

}



