.themoin-remit-form-recipients {
  @extend .themoin-recipients;

  .list {
    padding: 10px 0;
  }

  .recipient-card {
    position: relative;

    .edit, .delete {
      width: 40px;
      height: 40px;
      display: flex;
      align-items: center;
      justify-content: center;
    }

    .edit {
      position: absolute;
      top: 13px;
      right: 15px;
    }

    .delete {
      margin-right: -10px;
    }
  }

  .sender-simple, .sender-detail {
    padding: 0;
  }

  .sender-simple {
    margin: 30px 0;
    text-align: center;
    text-decoration: underline;
  }

  .sender-detail {
    display: flex;
    flex-wrap: wrap;

    button:first-child {
      margin-right: 20px;
    }

    flex-direction: row;
    @media (max-width: $max-width) {
      flex-direction: column;

      button:first-child {
        margin-right: 0;
        margin-bottom: 10px;
      }
    }

    button {
      width: 100%;
      min-height: 60px;
      flex: 1 1 0px;
      background: none;
    }

    .btn-sender-new {
      color: #22b1dd;
      border: solid 1px #22b1dd;
    }

    .btn-sender-list {
      color: #5451a6;
      border: solid 1px #5451a6;
    }
  }
}


