$border-top: 1px #dbdbdf solid;

.themoin-footer {
  @extend .themoin-container;
  background-color: $pale;
  border-top: $border-top;

  > div {
    display: flex;
    flex-direction: column;
    @media (max-width: $max-width) {
      width: calc(100% - 32px);
    }
  }

  h1, h2, h3, p {
    margin: 0;
    color: $deep-grey;
  }

  .mobile {
    display: none;
    @media (max-width: $max-width) {
      display: inline;
    }
  }

  .desktop {
    display: block;
    @media (max-width: $max-width) {
      display: none;
    }
  }

  @media (max-width: $max-width) {
    p {
      font-size: 10px;
    }
  }

  .spacer {
    flex-grow: 1;
    @media (max-width: $max-width) {
      display: none;
    }
  }

  .moin-foot-info {
    font-size: 12px;
    line-height: 2;
    letter-spacing: -0.9px;
    color: $deep-grey;
  }

  .moin-foot-link > a {
    line-height: 1;
    color: $deep-grey;
    text-decoration: none;
  }

  .section {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    @media (max-width: $max-width) {
      justify-content: flex-start;
      flex-direction: column;
    }
    width: 100%;

    &.top {
      padding: 26px 0 17px 0;
      @media (max-width: $max-width) {
        padding-top: 10px;
      }

      > div { // contact, account, links
        padding-top: 30px;
        @media (max-width: $max-width) {
          padding-top: 0;
        }
      }
    }

    &.bottom {
      padding: 42px 0 57px;
      border-top: $border-top;
      @media (max-width: $max-width) {
        padding: 20px 0 24px;
        align-items: center;
      }
    }
  }

  // top section > .contact | .account | .links
  .account {
    /* disabled */
    display: none;
  }

  .contact, .account {
    color: $deep-grey;
    flex-grow: 1;

    @media (max-width: $max-width) {
      h3 {
        margin-bottom: 11px;
        > span {
          margin-left: 14px;
        }
      }
    }
  }

  .contact {
    max-width: 300px;
    @media (max-width: $max-width) {
      margin-top: 30px;
    }
  }

  .links {
    display: flex;
    flex-direction: column;
    min-width: 140px;

    a {
      margin-bottom: 20px;
      white-space: nowrap;
    }

    @media (max-width: $max-width) {
      margin-top: 25px;
      width: 300px;
      flex-direction: row;
      flex-wrap: wrap;

      a {
        margin: 0 20px 17px 0;
        color: $dark-blue;
      }
    }
  }

  // bottom section > .company | .social
  .company {
    min-width: 360px;
    @media (max-width: $max-width) {
      width: 100%;
      min-width: 250px;
    }

    > img {
      margin-bottom: 14px;

      @media (max-width: $max-width) {
        display: none;
      }
    }
  }

  .social {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: flex-start;

    a {
      margin-right: 28px;
      padding-top: 44px;

      &:last-child {
        padding-top: 30px;
      }
    }

    @media (max-width: $max-width) {
      a {
        margin-right:0;
        padding-top: 25px;

        &:first-child {
          margin-right: 12px;
        }        
      }

      .facebook, .blog {
        width: 24px;
        height: 24px;
      }

      .yellow-id {
        display: none;
      }
    }
  }
}



