.themoin-container {
  width: 100%;

  display: flex;
  flex-direction: column;
  align-items: center;

  > div {
    width: 100%;
    @media (max-width: 960px) {
      width: calc(100% - 32px);
    }

    max-width: 960px;
  }
}

.reset-bootstrap {
  h1, h2, h3, p {
    margin: 0;
    padding: 0;
  }
}

/* Login, SignUp */
.modal-text {
  padding-top: 18px;
  padding-bottom: 20px;
  white-space: nowrap;
}

.popup {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  z-index: 2000;
  background-color: rgba(0, 0, 0, 0.5);

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  > div {
    background-color: white;
  }
}


