.themoin-main {
  > div {
    @extend .themoin-container;
  }

  .remits {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .user-sendlist {
    transition: height 0.4s ease-in-out;
    border: solid 1px #dbdbdf;
    padding: 15px;
    margin: 5px 20px;
    overflow: hidden;
    cursor: pointer;

    &:hover {
      background-color: #F3F4F7;
    }
  }

  .user-sendlist-num {
    font-weight: 100;
    font-size: 64px;
    line-height: 1;
    color: #dbdbdf;
    margin: -5px 10px 0 10px;
    width: 72px;
    
    @media (max-width: 850px) {
      display: none;
    }
  }

  .user-sendlistsimple {
    width: 100%;
    height: 120px;
    display: flex;
    text-align: center;
    flex-direction: row;
    align-items: center;

    @media (max-width: $max-width) {
      flex-direction: column;
      align-items: flex-start;
      height: auto;

      .desktop {
        display: none;
      }

      .user-sendlist-namedate {
        margin: 0;
      }

      .user-sendlist-amount {
        flex-grow: 0;
        height: auto;
        margin: 11px 0;

        .user-sendlist-ic {
          width: 6px;
          margin: 3px 7px;
        }
      }

      .user-sendlist-status {
        width: auto;
        .user-sendlist-state {
          width: auto;
          margin: 0;
        }
      }
    }
  }

  .user-sendlist-namedate {
    margin: 0 10px;
    text-align: left;
    width: 150px;
  }

  .user-sendlist-name {
    color: #4a4a4a;
  }

  .user-sendlist-date {
    line-height: 1;
    margin-top: 11px;
  }

  .user-sendlist-bar {
    width: 1px;
    height: 60px;
    margin: 0 10px;
    background-color: #dbdbdf;
  }

  .user-sendlist-amount {
    flex-grow: 1;
    text-align: left;
    margin: 0 10px;
  }

  .user-sendlist-sendunit, .user-sendlist-recieveunit {
    padding-left : 5px;
    color: #a0a7b8;
  }

  .user-sendlist-ic {
    width: 6px;
    margin: 6px 15px;
  }

  .user-sendlist-status {
    width: 206px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    > :nth-child(1) {
      width: 94px;
      margin: 0 10px;
    }
    > :nth-child(2) {
      width: 72px;
      margin: 0 10px;
    }
  }

  .user-sendlist-state {
    display: block;
  }

  .user-sendlist-state-text {
    line-height: 1;
    color: #292767;
    margin: 0px;
  }

  .user-sendlist-state-cancel {
    line-height: 1;
    color: #a0a7b8;
    text-decoration: underline;
    margin-top: 10px;
  }

  .user-sendlist-state-delete {
    line-height: 1;
    color: #a0a7b8;
    text-decoration: underline;
  }

  .user-sendlist-btn {
    width: 72px;
    height: 44px;
    line-height: 1;
    letter-spacing: -1px;
    color: #ffffff;
    margin: 0 10px;
  }

  .user-sendlistdetail {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    text-align: center;
    width: 100%;
    height: 275px;

    @media (max-width: $max-width) {
      flex-direction: column;

      .user-sendlistdetail-info {
        width: calc(100% - 30px);
      }
      .user-sendlistdetail-link {
        width: calc(100% - 30px);
        text-align: left;
        > button {
          margin: 0;
          margin-right: 30px;
        }
      }
    }
  }

  .user-sendlistdetail-info {
    flex-grow: 1;
    margin: 0 25px 0 10px;
    text-align: left;
  }

  .user-sendlistdetail-account {
    margin-top: 10px;
  }

  .user-sendlistdetail-name {
    color: #4a4a4a;
  }

  .user-sendlistdetail-amount {
    margin: 25px 0;
  }

  .user-sendlist-date {
    line-height: 1;
    margin-top: 11px;
  }

  .user-sendlist-sendunit, .user-sendlist-recieveunit {
    padding-left : 5px;
    color: #a0a7b8;
  }

  .user-sendlist-ic {
    width: 6px;
    margin: 6px 15px;
  }

  .user-sendlistdetail-state-text {
    color: #292767;
  }

  .user-sendlistdetail-link {
    width: 210px;
  }

  .user-sendlistdetail-btn {
    width: 130px;
    height: 60px;
    line-height: 1;
    letter-spacing: -1px;
    color: #ffffff;
    margin: 0 40px;
    margin-bottom: 10px;
  }

  .user-sendlistdetail-state-cancle {
    line-height: 1;
    color: #a0a7b8;
    text-decoration: underline;
  }

  .user-sendlist-empty {
    margin: 100px auto;
    text-align: center;
  }

  .user-sendlist-empty > p {
    font-size: 20px;
    line-height: 1;
    letter-spacing: -1px;
    color: #292767;
    font-weight: 400;
    margin-bottom: 17px;
  }

  .user-sendlist-empty > a {
    font-size: 14px;
    line-height: 1.7;
    letter-spacing: -1px;
    text-align: center;
    color: #a0a7b8;
    text-decoration: underline;
    font-weight: 400;
  }

  .user-sendlist-roundbox {
    width: 100px;
    height: 30px;
    border-radius: 50px;
    border: solid 1px #dbdbdf;
    line-height: 1;
    padding-top: 6px;
    text-align: center;
    margin: 0 auto 12px auto;
    font-size: 14px;
    color: #a0a7b8;
  }

  .user-sendlist-call {
    font-size: 18px;
    line-height: 1;
    color: #a0a7b8;
  }

  .userLimit {
    p {
      margin: 26px 0 29px 0;
      text-align: center;
    }
  }

  .user-subtitle {
    margin: 0 auto;
    position: static;
  }
}


