.RemitFormRecipientsPhoto {
  text-align: center;
  h3 {
    margin-bottom: 17px;
  }

  .desktop {
    @media (max-width: $max-width) {
      display: none;
    }
  }

  .radioBtns {
    margin: 29px 0 27px 0; 
  }

  .selectType {
    > p {
      margin-bottom: 27px;
      color: $dark-blue;
    }
  }

  .typeList {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    .photoType {
      width: 300px;
      margin: 8px 10px;
      padding: 16px 20px;
      border: solid 1px $grey;
    }

    .typeSelected {
      border: solid 1px $dark-blue;
    }
  }

  .personPhoto {
    margin-bottom: 42px;
  }

  .getPhoto {
    display: flex;
    justify-content: center;
    > div {
      width: 100%;
      max-width: 300px;
      margin: 0 10px;
    }

    @media (max-width: $max-width) { 
      flex-direction: column;
      align-items: center;

      > div {
        margin-bottom: 24px;
      }
    }
  }

  .remit-subtitle {
    text-align: left;
    margin-bottom: 17px;
    > span {
      width: 73px;
      height: 33px;
      border-radius: 50px;
      background-color: #ffffff;
      border: solid 1px #dbdbdf;
      line-height: 1;
      letter-spacing: -1px;
      text-align: center;
      color: #bac0cd;
      padding-top: 7px;
      margin: 0 11px 0 0;
      display: inline-block;
    }
  }

  .remit-pic {
    display: flex;
    align-items:center;
    justify-content:center;
	  border: solid 1px $line-grey;
  }
}


