.themoin-remit-success {
  @extend .themoin-container;

  h3 { margin: 0; }
  p { margin: 0; }

  > div {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;

    > p, a, div {
      width: 100%;
      @media (max-width: $max-width) {
        width: calc(100% - 32px);
      }
    }
  }

  img {
    @media (max-width: $max-width) {
      display: none;
    }
    margin-top: 80px;
    width: 43px;
    height: 44px;
  }

  h1 {
    margin-top: 20px;
    @media (max-width: $max-width) {
      margin-top: 37px;
    }
  }

  .intro {
    margin-top: 17px;
    margin-bottom: 0;
  }

  .box {
    margin: 40px 0;
    padding: 40px 10px;
    @media (max-width: $max-width) {
      margin: 17px 0;
      padding: 22px 10px;
    }

    max-width: 460px;

    border: solid 1px $light-grey;

    p { margin-bottom: 5px; }
    p.account {
      margin-top: 35px;
    }
  }

  .process {
    margin-bottom: 52px;
    @media (max-width: $max-width) {
      margin-bottom: 23px;
    }

    span {
      font-size: 16px;
      @media (max-width: $max-width) {
        font-size: inherit;
      }
      color: $dark-blue;
    }
  }

  a {
    max-width: 460px;
    height: 60px;
    margin-bottom: 180px;
    @media (max-width: $max-width) {
      margin-bottom: 22px;
    }
    color: $white;
    background-color: $purple;
    line-height: 60px;
  }

}


