.themoin-account {
  /* reset bootstrap */
  p { margin: 0; }

  > div {
    @extend .themoin-container;
  }

  .daum-container {
    z-index: 1040;
    display: flex;
    position: fixed;
    overflow-y: auto;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    width: 100%;
    max-width: 100%;
    background-color: rgba(0, 0, 0, 0.7);

    &.hidden {
      display: none;
    }

    .daum-layer {
      margin: auto;
      overflow-y: auto;
      width: calc(100vw - 10px);
      height: 100%;
      max-width: 400px;
      max-height: 500px;
      border: 1px solid;
      -webkit-overflow-scrolling: touch;
    }
  }

  .tab-container {
    border: solid 1px $line-grey;
    border-width: 0 0 1px 0;
  }

  .tab {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    height: 60px;
    @media (max-width: $max-width) {
      height: 50px;
    }

    > a {
      padding-top: 17px;
      @media (max-width: $max-width) {
        padding-top: 14px;
      }
      height: 100%;
    }

    > a:first-child {
      margin-right: 60px;
      @media (max-width: $max-width) {
        margin-right: 43px;
      }
    }

    > a.active {
      color: $dark-blue;
      border-color: $dark-blue;
      border-width: 0 0 4px 0;
      border-style: solid;
    }

    .spacer {
      flex-grow: 1;
    }

    p {
      margin-left: 10px;
      margin-top: -3px;
    }

    @media (max-width: $max-width) {
      img, p {
        display: none;
      }
    }
  }

  .content {
    display: flex;
    flex-direction: column;
    align-items: center;

    > h1 {
      margin: 30px 0;
      @media (max-width: $max-width) {
        margin: 20px 0;
      }
    }

    > p {
      text-align: center;
    }
  }

  .content form {
    width: 100%;
    max-width: 640px;
    min-width: 300px;
    padding: 20px 10px 70px 10px;
    @media (max-width: $max-width) {
      padding-top: 0;
      padding-bottom: 40px;
    }

    display: flex;
    flex-direction: column;
  }

  .btn-zipcode {
    color: $purple;
    border: solid 1px $purple;
    background-color: white;
    width:100%;
    height: 40px;
    margin-top: 20px;
  }

  .form-row {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    &.submit-row {
      margin: 30px 0;
    }

    > div {
      margin-top: 30px;
      padding-left: 10px;
      padding-right: 10px;
      flex-grow: 1;

      @media (max-width: $max-width) {
        margin-top: 20px;
      }
    }

    > .firstname, .lastname, .zip, .postcode {
      width: 40%;
      min-width: 135px;
      flex: 1 1 135px;
    }

    > .country, .address, .subaddress, .password, .repassword {
      width: 100%;
    }

    > .phone1 {
      min-width: 150px;
      flex-basis: 150px;
    }

    > .phone2 {
      min-width: 135px;
      flex: 1 1 135px;
    }
  }
}



